body {
  color: var(--main-dark-txt-cl);
  text-transform: uppercase;
  background-color: var(--main-light-bg);
  font-family: DM Sans, sans-serif;
  font-size: 14px;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  display: block;
}

img {
  max-width: 100%;
  height: auto;
  font-size: 0;
}

picture {
  font-size: 0;
  display: block;
  overflow: hidden;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

button {
  cursor: pointer;
  padding: 0;
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 375px) {
  .container {
    width: 375px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    width: 1440px;
    padding-left: 24px;
    padding-right: 24px;
  }
}

.section {
  padding-top: 40px;
  padding-bottom: 80px;
}

@media screen and (min-width: 768px) {
  .section {
    padding-bottom: 100px;
  }
}

@media screen and (min-width: 1440px) {
  .section {
    padding-top: 40px;
    padding-bottom: 72px;
  }
}

.section-title {
  letter-spacing: -.02em;
  font-size: 40px;
  font-weight: 500;
  line-height: 1;
}

@media screen and (min-width: 768px) {
  .section-title {
    font-size: 68px;
  }
}

.main-btn {
  height: 18px;
  letter-spacing: -.01em;
  text-transform: uppercase;
  color: var(--black-cl);
  border: 2px solid var(--btn-border-cl);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 40px;
  justify-content: center;
  align-items: center;
  padding: 14px 23px;
  font-family: DM Sans;
  font-weight: 700;
  line-height: 1.29;
  display: flex;
}

@media screen and (max-width: 374.98px) {
  .main-btn {
    font-size: 14px;
    line-height: 1.28;
  }
}

@media screen and (min-width: 768px) {
  .main-btn {
    height: 64px;
    padding: 20px 150px;
    font-size: 18px;
    line-height: 1.3;
  }
}

@media screen and (min-width: 1440px) {
  .main-btn {
    line-height: 1.333;
  }
}

.main-btn:hover, .main-btn:focus {
  color: var(--white-cl);
  background-color: var(--hover-light-cl);
}









:root {
  --white-cl: #fff;
  --black-cl: #111;
  --main-light-txt-cl: rgba(17, 17, 17, .6);
  --main-dark-txt-cl: rgba(255, 255, 255, .6);
  --btn-border-cl: #4f2ee8;
  --hover-light-cl: #4f2ee8;
  --hover-dark-cl: #eac645;
  --main-light-bg: #f6f6f6;
  --main-dark-bg: #202024;
  --z-index-header: 999;
  --z-index-hero-cont: 1;
  --z-index-rating-mark-block: 2;
}

